@import "../partials/variables";

//Theme 10
#atw_style10{
    .atw__title10{
        padding-bottom: 0;
        margin-bottom: 35px;
        &:before{
            content: none;
        }
        h3{
            display: inline-block;
            line-height: 50px;
            color: #202428;
            padding: 0 35px;
            background:#f5f5f5;
            border-radius: 5px;
        }
    }
    .atw--single_item{
        transition: $transition;
        &.atw_card_rounded{
            border-radius: 5px;
            overflow: hidden;
        }
        .atw_item_top{
            position: relative;
            .atw_item_view{
                position: absolute;
                width: 100%;
                left: 0;
                top: 0;
                height: 100%;
                display: flex;
                justify-content: center;
                align-items: center;
                transition: $transition;
                visibility: hidden;
                opacity: 0;
                a{
                    padding: 0 25px;
                    line-height: 40px;
                    background: #000;
                    color: #fff;
                    border-radius: 5px;
                    display: inline-block;
                    transition: $transition;
                    margin-top: 20px;
                }
            }
        }
        .atw_item_bottom{
            background: #f5f5f5;
            padding: 20px;
            display: flex;
            justify-content: space-between;
            align-items: center;
            transition: $transition;
            .atw_item_info{
                .atw_item_title{
                    font-size: 16px;
                    padding-bottom: 10px;
                    font-weight: 400;
                    a{
                        color: #363940;
                        transition: $transition;
                    }
                }
                .atw_price{
                    font-size: 18px;
                    font-weight: 400;
                    color: $theme1;
                }
            }
            .atw_cart_btn{
                a{
                    display: inline-block;
                    width: 40px;
                    height: 40px;
                    border-radius: 50%;
                    text-align: center;
                    color: $theme1;
                    font-size: 16px;
                    background: #fff;
                    padding-top: 11px;
                }
            }
        }
        &:hover{
            border-color: $theme1;
            .atw_item_top{
                .atw_item_view{
                    visibility: visible;
                    opacity: 1;
                    background: rgba(255,255,255,0.6);
                    a{
                        margin-top: 0;
                    }
                }
            }
            .atw_item_bottom{
                background: $theme1;
                .atw_item_info{
                    .atw_item_title a, .atw_price{
                        color: #fff;
                    }
                }
            }
        }
    }
    .owl-dots{
        text-align: center;
        width: 100%;
        margin-top: 45px;
        .owl-dot{
            display: inline-block;
            width: 10px;
            height: 10px;
            border-radius: 50%;
            background: transparentize($theme1, 0.8);
            transition: $transition;
            margin-right: 10px;
            &:last-child{
                margin-right: 0;
            }
            &.active{
                background: $theme1;
            }
        }
    }
}